import * as config from "../config.js";
import {Component} from 'react';
import axios from "axios";

function Test({title, year}){
    return(
        <li>
        <h1>{title}</h1>
        <h4>{year}</h4>
        </li>
    )  
}

class Query extends Component{

    state = {
        sort : "title",
        searchQuery : "",
        itemList : []
    }

    getItemList = async () => {
        this.setState({isLoading: true});
        var url = config.API_SERVER + "list_movies.json" + "?";
        if(this.state.searchQuery !== "") {
           url += "query_term=" + this.state.searchQuery + "&";
        }
        if(this.state.sort !== "") {
            url += "sort_by=" + this.state.sort + "&";
         }
        console.log(url);
        const {data: {data : {movies}}} = await axios.get(url);
        if(movies === undefined){
            this.setState({itemList:[], isLoading: false});
        }else{
            this.setState({itemList:movies, isLoading: false});
        }
        console.log(this.state.itemList);
        
    }

    componentDidMount(){
    }

    inputChange = (e) => {
        this.setState({
          [e.target.name]: e.target.value
        }); 
      }


    test = (props) => {
        this.getItemList();
    }

    render(){
        return( 
            <div className="search-qeury">
                <input type="text" name="searchQuery" onChange={this.inputChange}/>
                <button onClick={this.test} >검색</button>
                <select name="sort" onChange={this.inputChange}>
                <option value="title">title</option>
                <option value="year">year</option>
                </select>
                <ul className="Search-List">
              {this.state.isLoading ?  "목록 불러오는 중" : this.state.itemList.map((item, index) => {
                  return <Test title={item.title} year={item.year} key={index} />
              })}
          </ul>
            </div>
        );
      }
    
}

export default Query;