import * as config from "../config.js";
import {Component} from 'react';
import axios from "axios";

function Test({title, year}){
    return(
        <li>
        <h4>{title}</h4>
        <h4>{year}</h4>
        </li>
    )  
}

class List extends Component{
    state = {
        isLoading: true,
        itemList: []
    }

    getItemList = async () => {
        const url = config.API_SERVER + "list_movies.json";
        const {data: {data : {movies}}} = await axios.get(url);
        //this.setState({itemList:movies, isLoading: false});
    }
    componentDidMount(){
        this.getItemList();
    }

    render(){
        console.log(this.state.isLoading)
        return (
          <ul className="itemList">
              {this.state.isLoading ?  "목록 불러오는 중" : this.state.itemList.map((item, index) => {
                  return <Test title={item.title} year={item.year} key={index} />
              })}
          </ul>
        );
      }
    
}

export default List;